#center{
    position: absolute;
    right:1rem;
    top:1rem;
    color:darkgrey;
    z-index: 500;
    background-color:inherit;
    display: none;
    font-size: 3rem;
    opacity: 1;
    animation: fade 2s linear;
}


@keyframes fade {
  0% { opacity: 0 }
  100% { opacity: 1 }
}